.form {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    max-width: 600px;
  }
  
  .form__group {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }
  
  .form__label {
    font-size: 1rem;
    margin-bottom: 5px;
  }
  
  .form__input {
    padding: 10px;
    font-size: 1rem;
    border-radius: 4px;
    border: 1px solid #ccc;
  }
  
  .form__button {
    padding: 10px;
    font-size: 1rem;
    border-radius: 4px;
    background-color: #007bff;
    color: #fff;
    border: none;
    cursor: pointer;
  }
  
  .form__button:hover {
    background-color: #0069d9;
  }
  